<template>

  <div>
    <section class="vh-100" >
      <div class="py-5  pt-xl-5 mt-xl-5 h-100">

        <div class="px-5" style="color: #282828;">
          <div class="col-md-9 col-lg-12 col-xl-12">
            <div v-show="!showCardClass" class="ps-xl-2"><h4 class="title_weather_desktop px-xl-1 py-xl-1">Weather</h4></div>

            <div class="card mb-4 gradient-custom weatherHours" style="border-radius: 25px;">
              <div class="card-body p-4">
                <div id="demo1" class="carousel slide" data-ride="carousel">
                  <!-- Indicators -->
                  <ul class="carousel-indicators mb-0">
                    <li data-target="#demo1" data-slide-to="0" class="active"></li>
                    <li data-target="#demo1" data-slide-to="1"></li>
                    <li data-target="#demo1" data-slide-to="2"></li>
                  </ul>
                  <!-- Carousel inner -->
                  <div class="carousel-inner ">
                    <div class="carousel-item active">
                      <div class="d-flex justify-content-between mb-4 pb-2">
                        <div>
                          <h2 class="display-2 "><strong>{{ showWeaterTemp }}°C</strong></h2>
                          <p class="text-muted mb-0 city_title">{{ showNameWeather }}</p>
                        </div>
                        <div class="imgGlobal pt-1 d-flex align-items-center">
                          <img :src="showWeaterImg" width="150px">
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

              </div>
            </div>
            <div class=" mb-4  " style="border-radius: 25px;"
            :class="{'card' : showCardClass}">
              <div class="card-body p-4  p-lg-1">
                <div id="demo2" class="carousel slide" data-ride="carousel">
                  <!-- Indicators -->
                 
                  <!-- Carousel inner -->
                  <div class="carousel-inner ">
                    <div class="carousel-item active">
                      <div class="d-flex justify-content-around text-center  overflow">
                        <div v-for="(weather, i) in showHoursWeather" :key="i" class="flex-column hours">
                          <p class="small"><strong>{{ `${weather.feelslike_c} &#8451;` }}</strong></p>
                          <img :src="weather.condition.icon" alt="">
                          <p class="mb-0"><strong>{{ (weather.time).split(' ').pop() }}</strong></p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

              </div>
            </div>
            <div class="card weatherHours " style="border-radius: 25px;">
              <div class="card-body p-4 ">

                <div id="demo3" class="carousel slide " data-ride="carousel">
                  <!-- Indicators -->
                  <ul class="carousel-indicators mb-0">
                    <li data-target="#demo3" data-slide-to="0"></li>
                    <li data-target="#demo3" data-slide-to="1"></li>
                    <li data-target="#demo3" data-slide-to="2" class="active"></li>
                  </ul>
                  <!-- Carousel inner -->
                  <div class="carousel-inner">
                    <div class="carousel-item active">
                      <div class="d-flex justify-content-around text-center mb-4 pb-3 pt-2">
                        <div class="flex-column" v-for="(item, index) in showDaysWeather" :key="index">
                          <p class="small"><strong>{{ `${item.avgtemp_c} &#8451;` }}</strong></p>
                          <img :src="item.condition.icon" alt="">
                          <p class="mb-0"><strong>{{showDayWeek[index]}}</strong></p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import { mapGetters,mapActions } from 'vuex'
export default {
  computed: {
    ...mapGetters(['showWeaterTemp', 'showWeaterImg', 'allWeather', 'showHoursWeather', 'showNameWeather', 'showDaysWeather','showDayWeek']),
    showCardClass(){
      if (document.documentElement.scrollWidth <= 1024) {
        return true
      }
      return false
    }

  },
  methods:{
    ...mapActions(['weatherFetch'])
  }
  

}
</script>

<style scoped>
* {
  box-sizing: border-box;
}
section{
  background: #d6d6d7;
}

.weatherHours {
  background: white;
  display: block;
}

.imgGlobal img {
  width: 80px;
  height: 60px;
  object-fit: contain;

}

.gradient-custom {
  /* fallback for old browsers */
  background: #ddd;

  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(to right, rgba(255, 255, 255, 1), rgba(255, 236, 210, 1));

  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background: linear-gradient(to right, rgba(255, 255, 255, 1), rgba(255, 236, 210, 1))
}

.carousel-indicators li {
  background-color: #282828;
  width: 7px;
  height: 7px;
  border-radius: 50%;
}

.hours img {

  object-fit: cover;
}
.weatherDesktop{
  display: block;
}

@media(min-width: 1024px){
  section{
    background: #ffffff;
  }
  .weatherHours{
  display: none;
  }
  .title_weather_desktop{
    
    border-left: 5px solid #007bff;
    
  }
 

}


</style>