<template>
    <div class="main" >
        <div class=" px-2 py-2 border-bottom undersHeader"
       
        >
            <div class="news_title px-md-4 ">
                <h5>For you</h5>
            </div>
                <div v-if="!showWeaterTemp" class="fa-1x spiner pe-2 ">
                   <i class="fas fa-circle-notch fa-spin"></i>
                </div>
            <div v-else class="wearher d-flex " >
                <div class="wearher_img px-1">
                    <img :src="showWeaterImg" alt="">
                </div>
                <div class="wearher_temp ">
                    <strong >
                        {{showWeaterTemp}}°C
                    </strong>
                </div>
            </div>
        </div>
     
        

        <div 
           v-for="(news, i) in allNews" :key="i"
            class="news px-2 py-2 px-md-4 py-xl-1">
            <router-link 
                @click="setNews(news)"
                class="routerLink"
                :to="{name: 'newsPage', params:{ name: `${news.source.name}`} }"
            >
                <div class="d-flex ">
                        <div class="flex-shrink-0 img_news">
                            <img
                            v-show="news.urlToImage" 
                            :src="news.urlToImage" alt="">
                        </div>
                        <div class="content_news px-2 px-xl-3  py-xl-0 d-flex flex-column">
                            <div class="contect_category ">
                                <a :href="news.url">
                                    {{news.source.name}}
                                </a>
                            </div>
                            <div class="contect_title  py-xl-1">
                                <strong>{{news.title}}</strong>
                            </div>
                            <div v-show="!showWeatherDesktop" class="contect_description"> 
                                <span>{{news.description}}</span>
                            </div>
                            <div class="content_time_ago">
                                <p>{{news.dateAgo}}</p>
                            </div>
                
                        </div>
                </div>
               
            </router-link>
        </div>   
        
        
        
    </div>
    
</template>

<script>

import {mapGetters, mapMutations} from 'vuex'
export default {
    name:'NewsBlock',
    components:{
        
    },

    computed:{
        ...mapGetters(['allNews','allWeather','showWeaterTemp','showWeaterImg']),
        showWeatherDesktop(){
            
            if (document.documentElement.scrollWidth <= 1024) {
                return true
            }else{
                return false
            }
        }
    },
    methods: {
        
        ...mapMutations(['updateTargetNews','updateActiveClass']),
        setNews(news){
            this.updateActiveClass(true)
            this.updateTargetNews(news)
        }
    },
    watch:{
        clickNavBar(newV){
            if (newV) {
                return this.statusBar = 'block'
            }
            return ''
        }
    },
   
    mounted(){

  }
}
</script>

<style scoped>
       
*{
    margin: 0;
    
    
}
.news a{
    text-decoration: none;
    color: black;
}

.img_news img{
    width: 100px;
    height: 100px;
    object-fit:cover;
    border-radius: 10%;
}
.contect_title strong{
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    line-height: 20px;
    max-height: 60px;
    
}
.content_time_ago{
    color: #B7B7B7;
}
.contect_category a{
    color: #eb3b3b;
    text-decoration: none;
}
.contect_category a span{ 
    font-size: 12px;
    color: #B7B7B7;
    text-decoration: none;
}
.wearher_img img {
    width: 24px;
    height: 25px;
    object-fit:cover;
}
.main{
    padding-top: 65px;
}


.active{
    color: #eb3b3b;
    transform: scale(1.12);
    transition: 0.2s ease-in-out;
    
    
}
.undersHeader{
    display: flex;
    justify-content: space-between;
}


@media(min-width: 1024px){
    .main{
        min-width: 700px;
        border-left: 1px solid #d8d8d8;
    }
    .contect_category{
        font-size: 20px;
    }
    .contect_title {
        font-size: 22px;
    }
    .undersHeader{
        display: none;
    }
    .main{
        padding-top: 100px;
    }
    .news{
        border-radius: 25px;
        height: 200px;
    }
    .img_news img{
        width: 250px;
        height: 150px;
        object-fit:cover;
        border-radius: 10%;
    }
    .contect_description span{
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        line-height: 20px;
        max-height: 60px;
    }
}


</style>